<template>
    <div id="app">
        <header>
            <span>Masuk ke Dalam</span>
        </header>
        <main>
            <h2>Klik untuk kesejahteraan umat Rwanda</h2>
            <button id="btn" class="button is-primary" @click="getJokes">Get Jokes</button>
            <button id="btn" class="button danger">Hapus</button>

            <div v-if="loading">
                <img src="../src/assets/loader.gif"/>
                Loading.....
            </div>

            <section class="hero is-medium is-success">
                <div class="hero-body">
                    <div class="container">
                        <jokes-list :jokes="jokes"></jokes-list>
                    </div>
                </div>
            </section>

        </main>
    </div>
</template>

<script>
    import axios from 'axios';
    import JokesList from './JokesList.vue';

    export default {
        name: 'app',
        components: {
            'jokes-list': JokesList
        },
        data () {
            return {
                jokes: [],
                loading: false
            }
        },
        methods: {
            getJokes: function() {
                this.loading = true;
                axios.get('http://api.icndb.com/jokes/random/10').then(response =>  {
                    this.loading = false;
                    this.jokes = response.data.value;
                }).catch(error =>  {
                    console.log(error);
                    this.loading = false;
                })
            }
        }
    }
</script>

<style lang="scss">
    body {
        margin: 0;
    }

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
    }

    main {
        text-align: center;
        margin-top: 40px;
    }

    header {
        margin: 0;
        height: 56px;
        padding: 0 16px 0 24px;
        background-color: #35495E;
        color: #ffffff;
        span {
            display: block;
            position: relative;
            font-size: 20px;
            line-height: 1;
            letter-spacing: .02em;
            font-weight: 400;
            box-sizing: border-box;
            padding-top: 16px;
        }
    }

    button {
        background: #51B767;
        color: #ffffff;
        padding: 15px;
        border-radius: 0;
        font-weight: bold;
        font-size: 15px;
        border: 0;
    }

    .cards {
        background: #F5F5F5;
        height:400px;
        &:hover {
            transform: translateY(-0.5em);
            background: #EBEBEB;
        }
    }

    .cards {
        column-count: 1;
        column-gap: 1em;
        margin-top: 70px;
    }

    .columns {
        .column {
            flex-basis: 23%;
        }
    }
</style>



