<template lang="html">
    <section class="columns">

        <div class="column" v-for="(joke, index) in jokes" :key="joke.id" @click="deleteJokes(index)">
            <div class="card">
                <div class="card-image">
                    <figure class="image">
                        <img src="https://source.unsplash.com/random/" alt="Random images placeholder">
                    </figure>
                </div>
                <div class="card-content">
                    <div class="content">
                        <h3>{{ joke.id }}</h3>
                        <p>{{ joke.joke }}</p>
                        <p>{{ joke.categories }}</p>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
    export default {
        name: 'JokesList',
        props: ['jokes'],
        methods: {
            deleteJokes: function(index) {
                this.jokes.splice(index, 1)
                console.log(index)
            }
        }
    }
</script>

<style lang="scss">
    .columns {
        flex-wrap: wrap;
        justify-content: center;
        .column {
            flex-basis: 23%;
            flex-grow: 0;
            .card {
                height: 100%;
            }
        }
        .image {
            max-height: 200px;
            img {
                object-fit: cover;
            }
        }
    }
</style>



